import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';

import Element from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

import { library } from '@fortawesome/fontawesome-svg-core';
import { faUserSecret, faArrowLeft, faUser, faCircle, faUserLock,
   faLock, faPowerOff, faPencilAlt,
} from '@fortawesome/free-solid-svg-icons';
import {faQq} from '@fortawesome/free-brands-svg-icons';
import { faTimesCircle, faSquare } from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeIcon, FontAwesomeLayers, FontAwesomeLayersText } from '@fortawesome/vue-fontawesome'; // 三个库

library.add(
  faUserSecret, faArrowLeft, faUser, faQq,
  faCircle, faUserLock, faLock, faTimesCircle,
  faSquare, faPowerOff, faPencilAlt); // 从三个库引入的图片添加到本地的图库

Vue.component('font-awesome-icon', FontAwesomeIcon);
Vue.component('font-awesome-layers', FontAwesomeLayers);
Vue.component('font-awesome-layers-text', FontAwesomeLayersText);

Vue.use(Element);

import VueQuillEditor from 'vue-quill-editor';   // 富文本编辑
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';

Vue.use(VueQuillEditor);
Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
