<template>
    <div>
        <form class="login">
            <div class="logo"></div>
            <div class="container">
                <font-awesome-icon icon="user" class="icon"></font-awesome-icon>
                <input type="text" v-model="user.account" placeholder="用户名"/>
            </div>
            <div class="container">
                <font-awesome-icon icon="lock" class="icon"></font-awesome-icon>
                <input type="password" v-model="user.password" placeholder="密码">
            </div>
            <button @click.prevent="login">登&emsp;录</button>
        </form>
    </div>
</template>
<script>
import luobu from '../../help/login';
import cookie from '../../help/cookie';
import {messageBox} from '../../help/message';
export default {
    data(){
        return{
            user: {
                account: '',
                password: '',
            },
        };
    },
    created(){
    },
    methods:{
       login(){
           console.log(this.user)
           if(this.user.account === '' && this.user.password === ''){
               messageBox('用户名称和密码不能为空','warning',1500);
               return;
           }else if(this.user.account === ''){
               messageBox('用户名称不能为空','warning',1500);
               return;
           }else if(this.user.password === ''){
               messageBox('密码不能为空','warning',1500);
               return;
           }
           luobu.Login(this.user)
           .then(res=>{
               console.log(res);
               let message = res.data.message;
               if(res.data.code === -1){
                   messageBox(message,'warning',1500);
               }else if(res.data.code === 200){
                   let role = res.data.data.replace('该用户的角色为','');
                   let userData = {
                       userName: this.user.account,
                       roleName: role,
                   }
                   console.log(userData)
                   sessionStorage.setItem('userData',JSON.stringify(userData));
                   cookie.set('token',res.headers.token,3);
                   cookie.set('login', true, 3);
                   this.$router.push({
                       path: '/home',
                   })
                   messageBox(message,'success',1500);
               }
           })
           .catch(err=>{
               messageBox('服务器出错!','error',1500);
           })
       },
    },
}
</script>

<style lang="scss"scoped>
    .login {
        @include absolute-center(0,0);
        transform: translate(-50%, -50%);
        padding: 45px 26px 16px 26px;
        border-radius: 5px;
        color: #ccc;
        background: #fff;

        .logo{
            position: absolute;
            top: -38px;
            left: 97px;
            @include w-h(76px,76px);
            border-radius: 50%;
            background: url(../../assets/img/login-logo.png);
            box-shadow: 0 -3px 7px rgba(160, 160, 160, 0.5);
        }

        .container {
            margin: 10px 0;
            border: 1px solid;
            @include w-h(218px, 30px);
            @include flex(flex-start);
            border: 1px solid #ccc;
            border-radius: 5px;
            background-color: #e8f0fe;
            input{
            padding-left: 10px;
            border: none;
            border-left: 1px solid #ccc;
            color: #fd9f2a;
            outline: none;
            box-shadow: 0 0 0px 1000px #e8f0fe inset;
            &::placeholder{
                color: #ccc;
            }
            &:focus {
                border-color: #fd9f2a;
            }
        }
            
        }

        .icon{
            margin: 0 8px;
        }

        button{
            margin-top: 20px;
            padding: 11px 0;
            border-radius: 5px;
            width: 100%;
            color: #fff;
            background-color: #fd9f2a;
            border-color: #fd9f2a;
        }
    }


</style>