import Vue from 'vue';
import Router from 'vue-router';
import Login from './views/login/login.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login,
    },
    {
      path: '/home',
      redirect: '/home/welCome',
      component: () => import('./views/home.vue'),
      // props: true,
      children: [
        {
          path: 'welCome',
          name: 'welCome',
          component: () => import('./views/welCome.vue'),
        },
        {
          path: 'accountMange',
          name: 'accountMange',
          component: () => import('./views/adminMange/accountMange.vue'),
        },
        {
          path: 'changePassword',
          name: 'changePassword',
          component: () => import('./views/adminMange/changePassword.vue'),
        },
        {
          path: 'moduleMange',
          name: 'moduleMange',
          component: () => import('./views/adminMange/moduleMange.vue'),
        },
        {
          path: 'roleMange',
          name: 'roleMange',
          component: () => import('./views/adminMange/roleMange.vue'),
        },
      ],
    },
  ],
});
